
@import url("https://cdn.jsdelivr.net/gh/lipis/bootstrap-social@5.1.1/bootstrap-social.css");
button, button:focus, button:active {
  text-decoration: none !important;
  outline: none !important;
  -webkit-box-shadow: none !important;
          box-shadow: none !important;
  -webkit-tap-highlight-color: transparent !important;
}
@media (max-width: 321px) {
.display-4 {
    font-size: 2.5rem;
}
}
@media (max-width: 575.99px) {
.list-view {
    margin: 0 5px;
}
.collectionInteract-view {
    margin: 0 5px;
    padding-left: 5px;
    padding-right: 5px;
}
.collectionInteract-view button.my-btn-confirm {
      margin-left: 4px !important;
}
.collectionCreating-view {
    margin-left: 5px;
    margin-right: 5px;
}
.learning-card {
    margin-left: 5px;
    margin-right: 5px;
}
}
.jumbotron {
  /* FF3.6-15 */
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(135deg, rgba(255, 183, 0, 0.83) 0%, rgba(247, 89, 63, 0.8) 99%, rgba(247, 88, 64, 0.8) 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
}
.input-group {
  margin-bottom: 25px;
}
.my-header {
  margin-bottom: 25px;
}
.my-btn-confirm {
  border-color: #4dbb2c;
  font-weight: 500;
  color: #4dbb2c;
  font-size: 17px;
  background: #fff;
  /*#75da8b*/
}
.my-btn-confirm:hover {
    background: #4dbb2c;
    color: #fff;
}
.my-btn-orange {
  border-color: #ffb700;
  font-weight: 500;
  color: #ffb700;
  font-size: 17px;
  background: #fff;
}
.my-btn-orange:hover {
    background: #ffb700;
    color: #fff;
}
.my-btn-orange:hover {
    /* FF3.6-15 */
    /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(135deg, rgba(255, 183, 0, 0.83) 0%, rgba(247, 89, 63, 0.8) 99%, rgba(247, 88, 64, 0.8) 100%);
    /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
}
.my-btn-orange-active {
  /* FF3.6-15 */
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(135deg, rgba(255, 183, 0, 0.83) 0%, rgba(247, 89, 63, 0.8) 99%, rgba(247, 88, 64, 0.8) 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  color: #fff;
}
.my-btn-red {
  border-color: #f87c61;
  font-weight: 500;
  color: #f87c61;
  font-size: 17px;
  background: #fff;
}
.my-btn-red:hover {
    background: #f87c61;
    color: #fff;
}
.display-5 {
  font-size: 1.4em;
  color: #2b2a2a;
  word-wrap: break-word;
}
.lead {
  font-size: 24px;
  opacity: .7;
}
input.form-control:focus {
  border-color: #ffb700;
}
.btn-social {
  color: #fff !important;
}
.my-list-checkbox {
  cursor: pointer;
}
.collectionInteract-view input {
  border-top-right-radius: 0.25rem !important;
  border-bottom-right-radius: 0.25rem !important;
}
.bg-danger {
  background-color: #f87c61 !important;
}



